import React from 'react';
import {
    HashRouter,
    Switch,
    Route,
    NavLink,
} from "react-router-dom";
import styles from './App.module.css';
import Navigation from './pages/navigation/navigation';
import ReduxExample from './pages/counter/counter';

function App() {
    return (
        <div>
            <HashRouter basename="/">
                <Switch>
                    <Route path="/reviews">
                        <Navigation/>
                    </Route>
                    <Route path="/">
                        <Navigation/>
                    </Route>
                    <Route path="/counter">
                        <ReduxExample />
                    </Route>
                </Switch>
                <nav>
                    <NavLink to="/reviews">Reviews</NavLink>
                </nav>
            </HashRouter>
        </div>
    );
}

export default App;
